@import "_variable.scss";

html, body {
	@include font-size-map($html-font-size);
    font-family: HelveticaNeue, PingFangTC-Medium, Arial, Microsoft JhengHei, sans-serif;
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 20;
    height: 70px;
    text-align: center;
    background: $white;
    border-bottom: 1px solid $headerLine;
    padding: 0 4rem;
    .logo {
        position: absolute;
        top: 0;
        bottom: 0;
        cursor: pointer;
        img {
            width: auto;
            height: 38px;
            display: block;
            margin: calc((70px - 38px) / 2) 0;
        }
    }
    .menu {
        width: 100%;
        > li {
            @extend .fontsize2;
            position: relative;
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            text-indent: -10px;
            line-height: 70px;
            letter-spacing: 10px;
            direction: rtl;
            color: $menuText;
            cursor: pointer;
            padding: 0 0.5rem;
            &:not(:last-child) { margin-right: 2rem; }
            &:after {
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 8px;
                background: url($imgUrl+'tabBG.png');
                opacity: 0;
                transition: opacity .2s ease-in-out;
            }
            &:hover:after { opacity: 1; }
            &.active {
                &:after { opacity: 1; }
                > ul { display: block; }
            }
            > ul {
                @extend .fontsize-2;
                position: absolute;
                top: 70px;
                left: -1rem;
                right: -1rem;
                z-index: 19;
                display: none;
                list-style: none;
                background: $white;
                box-shadow: 0 2px 5px 0 rgba($headerLine, .5);
                padding: 0.5rem 1rem;
                li {
                    text-align: left;
                    padding: 1rem 0.6rem;
                    &+ li { border-top: 1px solid $menuLine; }
                }
            }
            > .arrow {
                width: 16px;
                height: 16px;
                display: none;
                background: url($imgUrl+'arrow.svg') no-repeat center / 1rem;
                transition: transform 0.3s;
                cursor: pointer;
            }
        }
    }
    .mobileBtn {
        width: 5rem;
        display: none;
        text-align: right;
        cursor: pointer;
        p {
            @extend .fontsize-2;
            display: inline-block;
            vertical-align: middle;
            color: $black;
            opacity: 0;
        }
        > div {
            position: relative;
            width: 1.5rem;
            height: 1.5rem;
            display: inline-block;
            vertical-align: middle;
            span {
                position: absolute;
                left: 0;
                right: 0;
                height: 2px;
                background: $black;
                transition: all 0.3s ease-in-out;
                &:nth-child(1) {
                    top: 2px;
                    transform-origin: top left;
                }
                &:nth-child(2) { top: 9px; }
                &:nth-child(3) { 
                    top: 16px;
                    transform-origin: bottom left;
                }
            }
        }
    }
}
.main {
    min-height: calc(100vh - 70px - 2rem - 1rem - 10vh);
    margin-top: 70px;
}
.footer {
    position: relative;
    z-index: 1;
    text-align: center;
    padding: 1rem 1rem calc(0.5rem + 10vh);
    p {
        text-align: center;
        font-size: 12px;
        color: $black;
    }
    .goTop {
        position: fixed;
        bottom: 10vh;
        right: 3rem;
        width: 3rem;
        height: 3rem;
        display: block;
        border-radius: 50%;
        background: url($imgUrl+'goTop.svg') no-repeat center / contain;
        cursor: pointer;
        visibility: hidden;
        transition: all 0.3s;
        &.show {
            transform: translateY(-1rem);
            visibility: visible;
        }
    }
    img {   
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 10;
        text-align: center;
        width: 100%;
        height: 10vh;
    }
}

.mtb_2 { margin: 2rem 0 !important; }
.mb_0 { margin-bottom: 0 !important; }
.textYellow { color: $lotteryYellow !important; }


// 首頁
.banner, .newsImg {
    img {
        height: auto;
        &.imgMobile { display: none; }
    }
    .owl-dots {
        text-align: center;
        .owl-dot {
            width: 8px;
            height: 8px;
            border-radius: 1rem;
            background: $menuBG;
            transition: all .3s ease-in-out;
            opacity: .3;
            margin: 0 3px;
            &.active {
                width: 2rem;
                opacity: .8;
            }
        }
    }
}
.memberAd {
    margin-bottom: 1rem;
    padding: 1rem 2rem;
    h2 {
        @extend .fontsize20;
        position: relative;
        width: fit-content;
        text-align: center;
        font-weight: bold;
        letter-spacing: 20px;
        color: $black;
        margin: 0 auto 1rem;
        padding: 1rem 4rem 1rem 4.5rem;
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 4rem;
            height: 4rem;
            background: url($imgUrl+'textL.svg') no-repeat center / contain;
        }
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            right: 0;
            width: 4rem;
            height: 4rem;
            background: url($imgUrl+'textR.svg') no-repeat center / contain;
        }
    }
    > div {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        img {
            width: 12rem;
            height: auto;
        }
    }
}
// 首頁&最新消息
.news {
    max-width: 900px;
    border-left: 1px solid $black;
    border-right: 1px solid $black;
    margin: 0 auto 5rem;
    padding: 1rem 4rem;
    h2 {
        @extend .fontsize10;
        text-align: center;
        font-weight: bold;
        color: $black;
        margin-bottom: 2rem;
    }
    > div {
        border-bottom: 1px solid $black;
        margin-bottom: 1rem;
        padding-bottom: 1rem;
    }
    p {
        @extend .fontsize0;
        letter-spacing: 0.57px;
        color: $black;
        margin-bottom: 0.5rem;
    }
    ul li {
        @extend .fontsize0;
        position: relative;
        list-style: none;
        letter-spacing: 0.57px;
        margin-bottom: 0.5rem;
        &:before {
            content: '';
            position: absolute;
            top: 3px;
            left: -1rem;
            width: 1rem;
            height: 14px;
            background: url($imgUrl+'listIcon.svg') no-repeat center left / contain;
        }
    }
    sub {
        vertical-align: super;
        font-size: 12px;
    }
}
// 最新消息
.newsImg {
    margin-bottom: 2rem;
    img {
        width: 100%;
        height: 100%;
    }
}
// 55年最新消息
.blackBG {
    color: $newYellow;
    background: $blackBG;
    .news {
        border-color: $newYellow;
        h2 {
            text-align: left;
            color: $newYellow;
            margin-bottom: 1rem;
        }
        p  { color: $newYellow; }
        .line {
            border-bottom: 1px solid rgba($newYellowLine, .5);
            margin-bottom: 1rem;
            padding-bottom: 1rem;
        }
        ul {
            li:before {
                position: absolute;
                top: 0;
                bottom: 0;
                height: auto;
                background: none;
            }
            &.listIcons li:before {
                @extend .fontsize16;
                content: '*';
            }
            &.listDecimal {
                padding-left: 1.4rem;
                p { margin-left: -1.4rem; }
                li { list-style: decimal; }
            }
        }
    }
    .lottery {
        position: relative;
        top: -3px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: url($imgUrl+'lotteryBG.png') no-repeat center right 20% / 20rem, linear-gradient(to bottom, #080300, #1f0700);
        margin-bottom: 4rem;
        padding: 2rem;
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 8px;
            background: url($imgUrl+'lotteryLine.jpg') repeat center;
        }
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 8px;
            background: url($imgUrl+'lotteryLine.jpg') repeat center;
        }
        p {
            @extend .fontsize8;
            color: $white;
            span {
                @extend .fontsize20;
                margin: 0 3px;
            }
        }
        form {
            width: 68%;
            display: flex;
            align-items: center;
            p {
                width: 10em;
                color: $lotteryYellow;
            }
            input:not([type="submit"]):not([type="button"]) {
                @extend .fontsize4;
                width: calc(((100% - 10em - 4em - 3rem) / 2) - 1rem);
                border: 2px solid #967040;
                background: url($imgUrl+'large-leather.png') repeat center / 10rem, $white;
                margin-right: 1rem;
                padding: 0.5rem;
                &::placeholder { color: $inputPlaceholder; }
            }
            button, input[type="submit"], input[type="button"] {
                @extend .fontsize4;
                width: 4em;
                color: $white;
                background: $lotteryYellowBtn;
                padding: 0.5rem;
                cursor: pointer;
            }
        }
    }
    &+ .footer {
        margin-top: -1.6rem;
        p { display: none; }
        .goTop { background-image: url($imgUrl+'goTop_y.svg'); }
        img { content: url($imgUrl+'footer_g.jpg'); }
    }
}
.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 20;
    display: none;
    .modalDialog {
        position: relative;
        top: 50%;
        left: 50%;
        width: 50%;
        display: block;
        text-align: center;
        color: $white;
        border-radius: 5px;
        background: rgba($blackBG, .9);
        transform: translate(-50%, -50%);
        padding: 4rem 3rem;
        .mobileBtn {
            position: absolute;
            top: 1.5rem;
            right: 1.5rem;
            cursor: pointer;
            div {
                position: relative;
                width: 1.5rem;
                height: 1.5rem;
                span {
                    position: absolute;
                    left: 0;
                    right: 0;
                    width: 25px;
                    height: 2px;
                    background: $white;
                    &:nth-of-type(1) {
                        top: 2px;
                        transform-origin: top left;
                        transform: rotate(45deg);
                    }
                    &:nth-last-of-type(1) { 
                        top: 19px;
                        transform-origin: bottom left;
                        transform: rotate(-45deg);
                    }
                }
            }
        }
        p {
            @extend .fontsize2;
            margin-bottom: 0.5rem;
        }
    }
    &.active { display: block; }
}

// 商品頁
.brandLogo, .OfficialWebsite {
    position: fixed;
    left: 8rem;
}
.brandLogo {
    top: calc(70px + 1rem);
    width: 15rem;
    img {
        width: 100%;
        height: auto;
        display: block;
    }
    h2 {
        @extend .fontsize-4;
        line-height: 1.2rem;
        letter-spacing: 1px;
        color: $productLogo;
        margin-top: 0.5rem;
    }
}
.OfficialWebsite {
    @extend .fontsize4;
    cursor:  pointer;
    top: calc(70px + 13.5rem + 1rem);
    width: 15rem;
    text-decoration: none;
    text-align: center;
    font-weight: bold;
    line-height: 2.5rem;
    letter-spacing: 8px;
    color: $white;
    border-radius: 2.5rem;
    opacity: 1;
    transition: opacity 0.3s;
    &:hover { opacity: 0.7; }
    &.btnD { top: calc(70px + 13.5rem + 9rem); }
}
.subMenu {
    @extend .fontsize6;
    color: $subMenu;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    list-style-type: none;
    text-align: right;
    padding: 3% 5%;
    li {
        position: relative;
        cursor:  pointer;
        writing-mode: tb-rl;
        -ms-writing-mode: tb-lr;
        -webkit-writing-mode: vertical-lr;
        -moz-writing-mode: vertical-lr;
        -ms-writing-mode: vertical-lr;
        writing-mode: vertical-lr;
        line-height: 2rem;
        letter-spacing: 9px;
        margin: 0 0.2rem;
        padding: 0 0.2rem;
        &:before {
            position: absolute;
            content: '';
            width: 1px;
            top: 0;
            bottom: 10px;
            right: 0;
            background: $white;
        }
        &:hover {
            &:before { background: $subMenu; }
        }
    }
}
.commodityList, .subMenu {
    width: calc(100vw - 15rem - 12rem);
    margin: 0 0 0 auto;
}
.commodityList {
    padding: 0 10% 0 6%;
    .item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 4rem;
        img {
            width: auto;
            display: block;
            margin: 0 auto;
        }
        .textBlock {
            width: calc(100% - 15rem);
            h3 {
                position: relative;
                @extend .fontsize2;
                font-weight: bold;
                letter-spacing: 3px;
                margin: 0 0 1rem;
                padding: 0 0 0.5rem;
                &:after {
                    position: absolute;
                    content: '';
                    left: 0;
                    right: 0;
                    bottom: 0;
                    height: 0.5px;
                }
            }
            .subTitle {
                @extend .fontsize0;
                line-height: 1.5rem;
                letter-spacing: 2px;
                color: $productText;
            }
            ul {
                list-style-type: none;
                margin: 0.5rem 0;
                li {
                    display: flex;
                    justify-content: space-between;
                    align-items: flex-start;
                    span {
                        @extend .fontsize0;
                        line-height: 1.5rem;
                        letter-spacing: 2px;
                        color: $productText;
                        &:nth-of-type(1) {
                            position: relative;
                            text-align-last: justify;
                            width: 5rem;
                            padding: 0 0 0 1.5rem;
                            &:before {
                                @extend .fontsize6;
                                position: absolute;
                                content: '';
                                top: calc((1.25rem - 0.8rem)/2);
                                left: 0;
                                width: 0.8rem;
                                height: 0.8rem;
                                background: url($imgUrl+'liIcon.svg')no-repeat center center / contain;
                            }
                        }
                        &:nth-of-type(2) { width: calc(100% - 5rem) }
                    }
                }
            }
            p {
                @extend .fontsize2;
                line-height: 1.5rem;
                letter-spacing: 2px;
                font-weight: bold;
                color: $productStress;
                margin: 0 0 0.5rem;
                > span {
                    @extend .fontsize24;
                    margin: 0 1rem;
                }
            }
            > p:nth-last-of-type(1) { letter-spacing: 12px; }
        }
    }
}
.Auchentoshan {
    .subMenu {
        li {
            &.active {
                color: $auchentoshan;
                &:before {
                    background: $auchentoshan;
                }
            }
        }
    }
    .commodityList {
        .item {
            .textBlock {
                h3 {
                    color: $auchentoshan;
                    &:after { background: $auchentoshan; }
                }
            }
        }
    }
    .OfficialWebsite {
        background: $auchentoshan;
    }
}
.Laphroaig {
    .subMenu {
        li {
            &.active {
                color: $laphroaic;
                &:before {
                    background: $laphroaic;
                }
            }
        }
    }
    .commodityList {
        .item {
            .textBlock {
                h3 {
                    color: $laphroaic;
                    &:after { background: $productLine; }
                }
            }
        }
    }
    .OfficialWebsite {
        background: $laphroaic;
    }
}
.otherColor {
    .subMenu {
        li {
            &.active {
                color: $product;
                &:before {
                    background: $product;
                }
            }
        }
    }
    .commodityList {
        .item {
            .textBlock {
                h3 {
                    color: $product;
                    &:after { background: $product; }
                }
            }
        }
    }
    .OfficialWebsite {
        background: $product;
    }
}

// 警語
.warningPage {
    min-height: calc(100vh - 2.5rem - 10vh);
    text-align: center;
    margin-top: 0;
    padding: 70px 0 0;
    img {
        width: 20%;
        min-width: 20rem;
        height: auto;
        margin: 0 auto 3rem;
    }
    p, span { color: $textGray; }
    p {
        @extend .fontsize24;
        letter-spacing: 27px;
        &.rwdText { display: none }
    }
    span {
        @extend .fontsize2;
        display: block;
        margin: 1rem;
    }
    .btn2 {
        display: flex;
        justify-content: center;
        width: 35%;
        margin: 3rem auto;
        button {
            @extend .fontsize24;
            cursor:  pointer;
            width: 35%;
            min-width: 10rem;
            color: $textGray;
            border: 1px solid $textGray;
            background: $white;
            filter: brightness(1);
            opacity: 1;
            transition: all 0.3s;
            margin: 0 1rem;
            padding: 1.5rem 0;
            &:hover {
                color: $white;
                border: 1px solid $goTopBG;
                background: $goTopBG;
            }
            &:active { opacity: 0.5; }
        }
    }
    ~ .footer button { display: none; }
}

@media screen and ( max-width: 1440px ) {
    .brandLogo, .OfficialWebsite { left: 4rem; }
    .commodityList, .subMenu { width: calc(100vw - 15rem - 4rem); }
}
@media screen and ( max-width: 1200px ) {
    .header {
        .logo { img { width: 10rem; } }
        .menu > li:not(:last-child) { margin-right: 1rem; }
    }
    .commodityList { padding: 0 5% 0 0; }
}
@media screen and ( max-width: 1050px ) {
    .header {
        padding: 0 2rem;
        .logo { margin-right: 3rem; }
    }
    .blackBG .lottery {
        flex-wrap: wrap;
        background-position: right;
        background-size: 26rem;
        form { width: 100%; }
    }
}
@media screen and ( max-width: 940px ) {
    .header {
        padding: 0 1rem;
        .logo { margin-right: 2rem; }
        .menu > li {
            text-indent: -5px;
            letter-spacing: 5px;
            &:not(:last-child) { margin-right: 0.5rem; }
        }
    }
    .brandLogo, .OfficialWebsite {
        width: 12rem;
        left: 2rem;
    }
    .OfficialWebsite { letter-spacing: 4px; }
    .commodityList, .subMenu { width: calc(100vw - 14rem ); }
}
@media screen and ( max-width: 900px ) {
    .header {
        .logo { img { width: 8.5rem; } }
    }
    .footer .goTop { right: 2rem; }
    .news { border: none; }
}
@media screen and ( max-width: 768px ) {
    .header {
        height: 50px;
        display: flex;
        justify-content: space-between;
        padding: 1rem 2rem;
        .logo {
            position: relative;
            top: initial;
            img {
                min-width: 145px;
                height: 25px;
                margin: 0;
            }
        }
        .menu {
            position: fixed;
            top: 50px;
            right: -100%;
            z-index: 19;
            width: 100%;
            height: calc(100vh - 50px);
            display: block;
            border-top: 1px solid $headerLine;
            background: rgba($menuBG, .95);
            transition: all 0.3s;
            padding: 1.5rem;
            > li {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                text-indent: 0;
                font-size: 1rem;
                line-height: unset;
                letter-spacing: 10px;
                direction: unset;
                color: $white;
                border-bottom: 1px solid $white;
                padding: 1.2rem 0.5rem;
                &:after { content: none; }
                > .arrow { display: block; }
                > ul {
                    position: inherit;
                    top: unset;
                    left: unset;
                    right: unset;
                    width: 100%;
                    text-align: left;
                    background: none;
                    box-shadow: none;
                    margin-top: 0.5rem;
                    padding: 0;
                    li {
                        letter-spacing: 12px;
                        padding: 0.5rem 0;
                        &+ li { border-top: none; }
                    }
                }
                &.active {
                    > ul {display: block; }
                    > .arrow { transform: rotate(-180deg); }
                }
            }
        }
        .mobileBtn { display: block; }
        &.active {
            .menu { right: 0; }
            .mobileBtn {
                p { opacity: 1; }
                > div span {
                    &:nth-child(1) { transform: rotate(45deg); }
                    &:nth-child(2) { opacity: 0; }
                    &:nth-child(3) { transform: rotate(-45deg); }
                }
            }
        }
    }
    .main:not(.warningPage) { margin-top: 50px; }
    .banner img, .newsImg img {
        &.imgDesk { display: none; }
        &.imgMobile { display: block; }
    }
    .brandLogo, .OfficialWebsite, .btnD {
        position: relative;
        display: block;
        top: inherit !important;
        left: inherit;
        width: 80%;
        margin: 0 auto 2rem;
    }
    .subMenu { display: none; }
    .commodityList {
        width: 100%;
        padding: 0 4rem 0 2rem;
    }

    .blackBG {
        .lottery {
            padding: 2rem 1.5rem;
            form {
                flex-wrap: wrap;
                margin-bottom: 5px;
                p {
                    width: 100%;
                    margin-bottom: 5px;
                }
                input:not([type=submit]):not([type=button]) {
                    width: calc((100% - 4em) / 2 - 1rem);
                }
            }
            > p span { font-size: unset; }
        }
        &+ .footer p { display: none; }
    }
    .modal .modalDialog { width: 80%; }
}
@media screen and ( max-width: 600px ) {
    .main {
        &.Auchentoshan, &.Laphroaig, &.otherColor { padding: 0 8%; }
    }
    .brandLogo, .OfficialWebsite {
        width: 100%;
        margin: 0;
    }
    .brandLogo {
        margin-bottom: 2rem;
        img {
            width: 80%;
            margin: 0 auto 1rem;
        }
    }
    .OfficialWebsite {
        display: block;
        width: 80%;
        margin: 1rem auto;
    }
    .commodityList {
        padding: 0;
        .item {
            position: relative;
            display: block;
            margin-bottom: 2rem;
            padding: 4rem 0 0;
            img {
                display: block;
                margin: 0 auto;
            }
            .textBlock {
                width: 100%;
                h3 {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    text-align: center;
                }
            }
        }
    }
    .warningPage {
        position: relative;
        z-index: 1;
        padding: 2rem;
        img {
            margin: 3rem auto;
        }
        
        p {
            display: none;
            font-size: 2rem;
            line-height: 4rem;
            letter-spacing: 10px;
            &.rwdText { display: block; }
        }
        span {
            font-size: 0.857rem;
            letter-spacing: 2px;
            margin: 0;
        }
        .btn2 {
            width: 100%;
            margin-bottom: 1rem;
            button {
                width: 50%;
            }
        }
    }
}
@media screen and ( max-width: 500px ) {
    .memberAd h2 { letter-spacing: 5px; }
    .news { padding: 0 3rem; }
}
@media screen and ( max-width: 416px ) {
    .warningPage { min-height: calc(100vh - 4rem - 10vh); }
}
@media screen and ( max-width: 400px ) {
    .memberAd {
        padding: 1rem;
        > div img { width: 50%; }
    }
    .news { padding: 0 2rem; }
}

